<template>
  <div id="app">
    <Header></Header>
    <Coins
    v-if = geckoResponse
    :coinData = "coinData">
    </Coins>
    <hr>
    <LocalMarkets
    v-if = lbtcResponse
    :localData = "localData">
    </LocalMarkets>
    <hr>
    <section>
      <b-container>
        <b-row>
          <LBTCBuy
          v-if = "LBTCBuyData.length"
          :LBTCBuy = "LBTCBuyData">
          </LBTCBuy>
          <PaxfulBuy
          v-if = "paxfulBuyData.length"
          :paxfulBuy = "paxfulBuyData">

          </PaxfulBuy>
        </b-row>
      </b-container>
    </section>
    <hr>
    <section>
      <b-container>
        <b-row>
          <LBTCSell
          v-if = "LBTCSellData.length"
          :LBTCSell = "LBTCSellData">
          </LBTCSell>
          <PaxfulSell
          v-if = "paxfulSellData.length"
          :paxfulSell = "paxfulSellData"></PaxfulSell>
        </b-row>
      </b-container>
    </section>
    <hr>
    <NewsBanner></NewsBanner>
    <hr>
    <News
    v-if = "articleNews.length"
    :articleNews = "articleNews"
    ></News>
    <hr>
    <Footer></Footer>
  </div>
</template>

<script>

import Header from './components/Header'
import Coins from './components/Coins'
import LocalMarkets from './components/LocalMarkets'
import LBTCBuy from './components/LBTCBuy'
import PaxfulBuy from './components/PaxfulBuy'
import LBTCSell from './components/LBTCSell'
import PaxfulSell from './components/PaxfulSell'
import NewsBanner from './components/NewsBanner'
import News from './components/News'
import Footer from './components/Footer'

export default {
  name: 'App',
  data() {
    return {
      coinData: {},
      geckoResponse: false,
      lbtcResponse:false,
      articleNews:[],
      currencyData: {},
      localData: null,
      LBTCBuyData: [],
      paxfulBuyData:[],
      LBTCSellData: [],
      paxfulSellData: []
      }
  },
  components: {
    Header,
    Coins,
    LocalMarkets,
    LBTCBuy,
    PaxfulBuy,
    LBTCSell,
    PaxfulSell,
    NewsBanner,
    News,
    Footer
  },
  mounted: function (){
    //show the loader until data is retrieved from Coingecko - Bitcoin and Ethereum in USD
    this.$loading(true)
    fetch('https://api.coingecko.com/api/v3/simple/price?ids=Bitcoin%2CEthereum&vs_currencies=USD&include_market_cap=true&include_24hr_vol=true&include_24hr_change=true&include_last_updated_at=true',{
      method: 'get'
    })
      .then((response) => {
        return response.json()
    } )
      .then((jsonData) => {
        this.coinData = jsonData
        this.geckoResponse = true
        this.$loading(false)
        this.getcurrencyData()
        this.getArticles()
        
        
      });

  },
  methods:{
    //retrieve crypto news from robacoin.com - latest 3
    getArticles(){
      fetch('https://www.robacoin.com/api/cryptonews/?format=json',{
        method: 'get'
      })
      .then((response) =>{
        return response.json()
      })
      .then((articles) => {
        this.articleNews = articles
      })
      
    },
    //get the IP address and local currency of the client - IP from request currency data from geoplugin
    //geoplugin not configured for CORS (can't send get request with JS)
    getcurrencyData(){
      fetch('https://www.robacoin.com/api/ip/?format=json',{
        method: 'get'
      })
      .then((response) => {
        return response.json()
      })
      .then((currencydataJSON) =>{
        this.currencyData = currencydataJSON;
        this.getlocalData(this.currencyData.geoplugin_currencyCode)
      })
    },
    //get the market price of Bitcoin in the client local currency
    getlocalData(localCurrency) {
      let url = 'https://www.robacoin.com/api/localprice/' + localCurrency + '/?format=json' 
      fetch(url,{
        method: 'get'
      })
      .then ((response) => {
        return response.json()
      })
      .then ((localPrice) =>{
        this.localData = localPrice
        this.lbtcResponse = true
        this.getLBTCBuy(localCurrency)
      })
    },
    //get localbitcoins buy ads for the local currency (top 3)
    getLBTCBuy(localCurrency){
      let url = 'https://www.robacoin.com/api/lbtcbuy/'+ localCurrency +'/?format=json'
      fetch(url,{
        method: 'get'
      })
      .then ((response) =>{
        return response.json()
      })
      .then ((responseJSON) =>{
        this.LBTCBuyData = responseJSON
        this.getPaxfulBuy(localCurrency)
      })
    },
    //get paxful buy ads for the local currency (top 3)
    getPaxfulBuy(localCurrency){
      let url = 'https://www.robacoin.com/api/paxfulbuy/'+ localCurrency +'/?format=json'
      fetch(url,{
        method: 'get'
      })
      .then ((response) =>{
        return response.json()
      })
      .then ((responseJSON) =>{
        this.paxfulBuyData = responseJSON
        this.getLBTCSell(localCurrency)

      })
    },
    //get localbitcoins sell ads for the local currency (top 3)
    getLBTCSell(localCurrency){
      let url = 'https://www.robacoin.com/api/lbtcsell/'+ localCurrency +'/?format=json'
      fetch(url,{
        method: 'get'
      })
      .then ((response) =>{
        return response.json()
      })
      .then ((responseJSON) =>{
        this.LBTCSellData = responseJSON
        this.getPaxfulSell(localCurrency)
      })

    },
    //get paxful sell ads for the local currency (top 3)
    getPaxfulSell(localCurrency){
       let url = 'https://www.robacoin.com/api/paxfulsell/'+ localCurrency +'/?format=json'
      fetch(url,{
        method: 'get'
      })
      .then ((response) =>{
        return response.json()
      })
      .then ((responseJSON) =>{
        this.paxfulSellData = responseJSON
      })
    }
   
  }
}
  
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
