<template>
    <b-col sm="6">
        <div>
            <fieldset>
                <a href="https://localbitcoins.com/?ch=15s53" target="blank"><legend style="color: orange;">Sell On Localbitcoins.com</legend></a>
                
                <div class="card">
                    <div class="card-body">
                        <div class="table-responsive">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>Payment Method</th>
                                        <th>Buying Price</th>
                                        <th>Premium</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!--I could have used a v-for directive but I don't want to. Probably because I am returning only 3 items and the last item is custom for current price in USD -->
                                    <!-- for alot of data, use a v-for directive-->
                                    <tr>
                                        <td>{{LBTCSell[0].data.online_provider}}: <strong>{{LBTCSell[0].data.bank_name}}</strong></td>
                                        <td>{{LBTCSell[0].data.currency}} {{LBTCSell[0].data.temp_price |readable}}</td>
                                        <td>{{LBTCSell[0].data.temp_price_usd| getPremium(LBTCSell[LBTCSell.length - 1].data)}}%</td>
                                    </tr>
                                    <tr>
                                        <td>{{LBTCSell[1].data.online_provider}}: <strong>{{LBTCSell[1].data.bank_name}}</strong></td>
                                        <td>{{LBTCSell[1].data.currency}} {{LBTCSell[1].data.temp_price |readable}}</td>
                                        <td>{{LBTCSell[1].data.temp_price_usd| getPremium(LBTCSell[LBTCSell.length - 1].data)}}%</td>
                                    </tr>
                                    <tr>
                                        <td>{{LBTCSell[2].data.online_provider}}: <strong>{{LBTCSell[2].data.bank_name}}</strong></td>
                                        <td>{{LBTCSell[2].data.currency}} {{LBTCSell[2].data.temp_price |readable}}</td>
                                        <td>{{LBTCSell[2].data.temp_price_usd| getPremium(LBTCSell[LBTCSell.length - 1].data)}}%</td>
                                    </tr>
                                    <tr></tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <hr>
                <a href="https://localbitcoins.com/?ch=15s53" target="blank"><button class="btn btn-dark btn-sm" type="button" style="color: orange;">Sell On Localbitcoins</button></a>
                <hr>
            </fieldset>
        </div>
    </b-col>
</template>

<script>
export default {
    props:{
        LBTCSell: Array
    },
    filters:{
        readable(value){
            return parseFloat(value).toLocaleString()
        },
        getPremium(value, args){
            return (((parseFloat(value) - parseFloat(args))/parseFloat(args))*100).toFixed(2)
        }
    }
}
</script>