<template>
    <div class="col" sm="6">
         <div>
            <fieldset>
                <a href="https://localbitcoins.com/?ch=15s53" target="blank"><legend style="color: orange;">Buy From Localbitcoins.com</legend></a>
                <div class="card">
                    <div class="card-body">
                        <div class="table-responsive">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>Payment Method</th>
                                        <th>Selling Price</th>
                                         <th>Premium</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!--I could have used a v-for directive but I don't want to. Probably because I am returning only 3 items and the last item is custom for current price in USD -->
                                    <!-- for alot of data, use a v-for directive-->
                                    <tr>
                                        <td>{{LBTCBuy[0].data.online_provider}}: <strong> {{LBTCBuy[0].data.bank_name}}</strong></td>
                                        <td>{{LBTCBuy[0].data.currency}} {{LBTCBuy[0].data.temp_price|readable}}</td>
                                        <td>{{LBTCBuy[0].data.temp_price_usd| getPremium(LBTCBuy[LBTCBuy.length - 1].data)}}%</td>
                                    </tr>
                                    <tr>
                                        <td>{{LBTCBuy[1].data.online_provider}}: <strong>{{LBTCBuy[1].data.bank_name}}</strong></td>
                                        <td>{{LBTCBuy[1].data.currency}} {{LBTCBuy[1].data.temp_price|readable}}</td>
                                        <td>{{LBTCBuy[1].data.temp_price_usd| getPremium(LBTCBuy[LBTCBuy.length - 1].data)}}%</td>
                                    </tr>
                                    <tr>
                                        <td>{{LBTCBuy[2].data.online_provider}}: <strong>{{LBTCBuy[2].data.bank_name}}</strong></td>
                                        <td>{{LBTCBuy[2].data.currency}} {{LBTCBuy[2].data.temp_price |readable}} </td>
                                        <td>{{LBTCBuy[2].data.temp_price_usd| getPremium(LBTCBuy[LBTCBuy.length - 1].data)}}%</td>
                                    </tr>
                                               
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <hr>
                <a href="https://localbitcoins.com/?ch=15s53" target="blank"><button class="btn btn-dark btn-sm" type="button" style="color: orange;">Buy From Localbitcoins</button></a>
                <hr>
            </fieldset>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        LBTCBuy : Array
    },
    filters:{
        readable(value){
            return parseFloat(value).toLocaleString()
        },
        getPremium(value, args){
            return (((parseFloat(value) - parseFloat(args))/parseFloat(args))*100).toFixed(2)
        }
    
    }
}
</script>
