<template>
  <div>
  <b-card-group deck>
    <b-card  header-tag="header">
      <template v-slot:header>
        <h6 class="mb-0">Bitcoin <i class="fab fa-bitcoin"></i> <span>   updated: <strong>{{coinData.bitcoin.last_updated_at |timeDifference}} mins</strong> ago</span></h6>
      </template>
      <b-card-text>
            <div>
            <b-table striped hover :items="BTCitems"></b-table>
            </div>
      </b-card-text>
      <template v-slot:footer>
        <em>Showing prices in <strong>USD</strong></em>
      </template>
    </b-card> 

    <b-card  header-tag="header">
      <template v-slot:header>
        <h6 class="mb-0">Ethereum <i class="fab fa-ethereum"></i> <span>   updated: <strong>{{coinData.ethereum.last_updated_at |timeDifference}} mins</strong> ago</span></h6>
      </template>
      <b-card-text>
            <div>
            <b-table striped hover :items="ETHitems"></b-table>
            </div>
      </b-card-text>
      <template v-slot:footer>
        <em>Showing prices in <strong>USD</strong></em>
      </template>
    </b-card>   
   


  </b-card-group>
</div>
</template>

<script>
export default {
    data() {
        return {
            BTCitems: [
          { price: this.coinData.bitcoin.usd , change_24h: (this.coinData.bitcoin.usd_24h_change).toFixed(2)+ '%',
           _cellVariants: { change_24h: this.changeChecker(this.coinData.bitcoin.usd_24h_change)}} 
        ],
        ETHitems: [
          { price: this.coinData.ethereum.usd , change_24h: (this.coinData.ethereum.usd_24h_change).toFixed(2)+ '%',
           _cellVariants: { change_24h: this.changeChecker(this.coinData.ethereum.usd_24h_change)}} 
        ]
        }
    },
    props:{
        coinData: Object
},
    methods:{
        changeChecker (value) {
            if (value < 0){
                return 'danger'
            }
            else{
                return 'success'
            }
        }
    },
    filters:{
        readable: function(value){
            return value.toLocaleString()
        },
        timeDifference: function(value){
            let difference = Math.floor(((Date.now()/1000) - value)/60)
            if (difference < 0){
              return 0
            }
            else{ return difference}
        }
    }
}
</script>

<style scoped>
.price-drop {
    color:red;
}
.price-gain {
    color:green;
}
</style>